.portfolioBlock {
    min-height: 100vh;
    background-color: #FFFF00;
}

.portfolioContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;

}

.title {
    background-color: #53a0b1;
}

.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

